import { Dimension } from '../UNSAFE_visTypes/common';

export const inSpace = (x: number, y: number, dimensions: Dimension) => {
  const minX = dimensions.x;
  const minY = dimensions.y;
  const maxX = dimensions.x + dimensions.width;
  const maxY = dimensions.y + dimensions.height;
  if (x < minX || x > maxX || y < minY || y > maxY) {
    return false;
  }
  return true;
};
